body {
  background-color: #00172A;
  color: #fff;
}

#login #login_error {
  color: #1A2338;
}
#login .message {
  color: #1A2338;
}
#login h1 {
  margin-bottom: 40px;
}
#login h1 a {
  width: 270px;
  height: 158px;
  background-size: 270px 158px;
}
#login h1 a:focus {
  box-shadow: none;
}
#login form {
  border-radius: 0px;
  box-shadow: 0px 2px 9px 0px rgba(0, 0, 0, 0.1);
  border: none;
}
#login form label {
  color: #1A2338;
}
#login form .input {
  border: none;
  border-radius: 0px;
  box-shadow: none;
  background-color: #fff;
  border-bottom: 2px solid #C26538;
  font-weight: normal;
  color: #1A2338;
}
#login form input[type=submit] {
  color: #fff;
  background-color: #C26538;
  box-shadow: none;
  text-shadow: none;
  border-radius: 50px;
  padding: 5px 20px;
  border: none;
  transition: all 0.35s ease;
  font-size: 12px;
  text-transform: uppercase;
  font-weight: bold;
  transition: all 0.3s ease-in-out;
}
#login form input[type=submit]:hover {
  background: #1A2338;
}
#login #backtoblog, #login #nav {
  text-align: center;
}
#login #backtoblog a, #login #nav a {
  color: #fff;
  font-weight: 600;
}
#login input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 30px white inset;
  -webkit-text-fill-color: #1A2338 !important;
}